<template>
  <div class="permission-setting">
    <el-card>
      <div slot="header">
        <span class="card-name">预警权限设置</span>
      </div>
      <el-form>
        <el-form-item label-width="100px" label="样本名称">
          <sample-select v-on:update:selected="getVars" :init-sample.sync="initSample"></sample-select>
        </el-form-item>
        <el-form-item label-width="100px" label="请选择角色">
          <div class="role-tree">
            <el-tree
              :data="roleTree"
              :props="props"
              ref="tree"
              show-checkbox
              default-expand-all
              node-key="id"
            ></el-tree>
          </div>
        </el-form-item>
        <el-form-item label-width="100px" label="预警方式">
          <el-checkbox v-model="warningType" :true-label="1">邮箱</el-checkbox>
        </el-form-item>
        <el-form-item label-width="100px">
          <el-button type="primary" @click.prevent.stop="saveFunc">保存</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
/* eslint-disable */
import sampleSelect from '@/components/sample-select.vue';
import {getGroupUserInfo, saveWarningInfo} from '@/api/user.js';
export default {
  name: 'permission-setting',
  components: {
    sampleSelect
  },
  data() {
    return {
      initSample: [],
      roleTree: [],
      warningType: false,
      props: {
        children: 'children',
        label: 'label'
      },
      checkedKeys: []
    };
  },
  methods: {
    getVars(newV) {
      this.modulename = newV[2];
      this.roleTree = [];
      this.checkedKeys = [];
      getGroupUserInfo(this.modulename)
        .then(result => {
          if (result.result) {
            let id = 0;
            Object.keys(result.result).forEach(key => {
              let children = result.result[key].map(item => {
                item.label = item.truename;
                item.isUser = true;
                item.id = id++;
                if (item.warningtype) {
                  this.checkedKeys.push(item.id);
                }
                return item;
              });
              this.roleTree.push({
                label: key,
                id: id++,
                children: children
              });
            });
            this.$nextTick(() => {
              this.$refs.tree.setCheckedKeys(this.checkedKeys);
            });
          }
        })
        .catch(err => console.log(err));
    },
    saveFunc() {
      if (!this.modulename) {
        this.$message.warning('请选择样本名称');
        return;
      }
      if (!this.warningType) {
        this.$message.warning('请选择报警方式');
        return;
      }
      let nodes = this.$refs.tree.getCheckedNodes();
      if (nodes.length === 0) {
        this.$message.warning('请选择角色');
        return;
      }
      let groupUserDetail = [];
      nodes
        .filter(item => item.isUser)
        .forEach(item => {
          const theOne = groupUserDetail.find(
            one => one.groupid === item.groupId
          );
          if (theOne) {
            theOne.useridList.push(item.userId);
          } else {
            groupUserDetail.push({
              groupid: item.groupId,
              useridList: [item.userId]
            });
          }
        });
      saveWarningInfo(this.modulename, this.warningType, groupUserDetail)
        .then(result => {
          if (result.statusCode === 0) {
            this.$message.success('保存成功');
          }
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-card {
  margin: 20px 15px;
}
</style>
<style lang="scss">
.permission-setting {
  .role-tree {
    height: 300px;
    width: max-content;
    min-width: 500px;
    margin-top: 12px;
    overflow-y: auto;
    border: 1px solid #409eff;
  }
  .el-tree {
    margin-left: 0;
  }
  .el-tree .el-tree-node__content {
    height: 26px;
  }
  .el-tree-node__expand-icon {
    margin-top: 0;
  }
}
</style>
